<template>
  <div class="seePage">
    <!--  查看调仓移库单 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>报表系统</el-breadcrumb-item>
      <el-breadcrumb-item>库存报表</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">库存业务出入库流水表</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>查看调仓移库单</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search_container">
      <el-form :inline="true" :model="TableData.info" class="demo-form-inline">
        <el-form-item label="调出组织：">
          <p style="width:170px">{{TableData.info.company_name}}</p>
        </el-form-item>
        <el-form-item label="调入组织：">
          <p style="width:170px">{{TableData.info.new_company_name}}</p>
        </el-form-item>
        <el-form-item label="业务日期：">
          <p style="width:170px">{{TableData.info.business_time}}</p>
        </el-form-item>
        <el-form-item label="审核状态：">
          <p style="width:170px">{{TableData.info.auditor_status_name}}</p>
        </el-form-item>
        <el-form-item label="单据号：">
          <p style="width:170px">{{TableData.info.transfer_sn}}</p>
        </el-form-item>
        <el-form-item label="创建人：">
          <p style="width:170px">{{TableData.info.add_user_name}}</p>
        </el-form-item>
        <el-form-item label="创建时间：">
          <p style="width:170px">{{TableData.info.add_time}}</p>
        </el-form-item>
        <el-form-item label="备注：">
          <p style="width:170px">{{TableData.info.remark}}</p>
        </el-form-item>
      </el-form>
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.goods_list" style="width: 100%" max-height="600">
        <el-table-column :show-overflow-tooltip="true" property="goods_id" min-width="160" label="商品编号"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_name" min-width="500" label="商品名称">
          <template slot-scope="scope">
            <pre>{{scope.row.goods_name}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="batch" label="年周/批次" min-width="130">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="warehouse_name" label="调出仓库" min-width="130">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="allocation_name" label="调出货位(前)" min-width="130">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_num" label="调出货位在库数量" min-width="130" align="right">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="new_warehouse_name" min-width="130" label="调入仓库">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="new_allocation_name" min-width="130" label="调入货位">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="new_goods_num" min-width="110" label="调入数量" align="right">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="update_time" min-width="170" label="最近修改时间">
        </el-table-column>
      </el-table>
    </div>
    <div class="page_container">
      <div class="pageNumber">每页显示</div>
      <el-pagination class="erp-pagination" @size-change="handleSizeChange" :page-size="pageSize1" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
      </el-pagination>
    </div>
    <div class="dialog-footer" style="text-align:center;margin-top:20px;">

      <!-- 未审核 -->
      <template v-if="TableData.info.auditor_status == 1">
        <el-button type="primary" @click="audit('transferWarehouseAuidtorPass')" v-if="authority.b_pass==1">审核</el-button>
        <el-button @click="audit('transferWarehouseAuditorInvalid')" v-if="authority.b_invalid==1">作废</el-button>
      </template>
      <!-- 作废 -->
      <template v-if="TableData.info.auditor_status == 2 || TableData.info.auditor_status == 3 || (authority.b_invalid != 1 && authority.b_pass != 1)">
        <el-button @click="goBack">返回</el-button>
      </template>
      <el-button @click="goBack" v-else>取消</el-button>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "reportTransferwareSee",
  data() {
    return {
      authority: {},
      TableData: {
        info: [],
        goods_list: []
      },
      params: {},
      per_page: [30, 50, 100, 500],
      pageSize: 30,
      warehouseList: {
        options: []
      }, //仓库列表
      allocationList: {
        options: []
      } //货位列表
    };
  },
  computed: {
    ...mapGetters(["transferwareIndexGet", "inventoryoutinTransferwareSeeGet"])
  },
  methods: {
    ...mapActions(["inventoryoutinIndexACT"]),
    goBack() {
      this.inventoryoutinIndexACT({
        moduleName: "Index"
      });
    },
    audit(api) {
      this.$axios({
        method: "post",
        url: this.$api[api],
        data: {
          transfer_id: this.params.transfer_id
        }
      }).then(res => {
        if (res.data.code == 200) {
          this.$message({
            message: res.data.data,
            type: "success"
          });
          this.goBack();
        }
      });
    },
    initData() {
      this.$axMethod.infoTransferBills(this.params).then(({ data }) => {
        if (data.code == "200") {
          console.log(data.data);
          this.TableData = data.data;
        }
      });
    },
    handleSizeChange(val) {
      this.params.per_page = val;
      this.initData();
    },
    handleCurrentChange(val) {
      this.params.page = val;
      this.initData();
    }
  },
  created() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.params = {
      transfer_id: this.inventoryoutinTransferwareSeeGet.transfer_id
    };
    this.$common.options("getWarehouseListOption", this.warehouseList, {
      company_id: this.TableData.info.company_id
    }); //仓库管理列表
    this.$common.options("getAllocationListOption", this.allocationList); //货位管理列表
    this.initData();
  }
};
</script>

<style scoped>
.title {
  line-height: 50px;
  border-bottom: 1px solid #dddddd;
}

.el-form-item {
  margin-right: 50px;
}

.tips li {
  float: right;
  line-height: 50px;
  margin-left: 15px;
}
</style>